import {
    Card, Col, Row, Alert, Image, Upload, Form, Button, Select,
    Table, Progress, Input, Typography, Divider, Space, Popconfirm
} from "antd";
import { InboxOutlined } from '@ant-design/icons';
import { useEffect, useState } from "react";
import { FileUpload } from "./Components";
const { ipcRenderer } = window.electron;
const { Dragger } = Upload;
const { Title, Paragraph } = Typography

export default function Home() {
    const [dataSource, setDataSource] = useState([]);
    const [files, setFiles] = useState([]);
    const [model, setModel] = useState('realesrgan-x4plus-anime');
    const fileLists = files ? Array.from(new Set(files.map(file => (file.path)))) : [];

    useEffect(() => {
        const tabView = [];
        fileLists.forEach(file => {
            tabView.push({
                origin: file,
                path: file,
                progress: 0,
            })
        });
        setDataSource(tabView);
        // console.log(fileLists);
    }, [files]);

    return (
        <>
            <Space size='middle' direction="vertical" style={{ width: '100%' }}>
                <Card title="">
                    <Row gutter={16}>
                        <Col span={10}>
                            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
                                <FileUpload files={files} setFiles={setFiles} />
                                <Select showSearch
                                    style={{ width: '100%' }}
                                    placeholder=""
                                    value={model}
                                    onChange={setModel}
                                    options={[{
                                        label: 'realesrgan-x4plus-anime',
                                        value: 'realesrgan-x4plus-anime'
                                    }, {
                                        label: 'realesrnet-x4plus',
                                        value: 'realesrnet-x4plus'
                                    }]}
                                />
                            </Space>
                        </Col>

                        <Col span={14}>
                            <Alert
                                message=""
                                description={
                                    <><a href="https://github.com/ArSrNa/ArESRGAN-GUI" target="_blank">github</a>
                                        <br />githubstar!!!</>
                                }
                                type="info"
                                showIcon
                            />
                        </Col>
                    </Row>
                </Card>

                <TView dataSource={dataSource} setDataSource={setDataSource} fileLists={fileLists} model={model} />

            </Space >
        </>
    )
}

function TView({
    dataSource, fileLists, setDataSource, model
}) {
    const [start, setStart] = useState(false);
    const changeColumn = {
        path(i, path) {
            setDataSource(prevDataSource => {
                const updatedDataSource = [...prevDataSource];
                updatedDataSource[i] = { ...updatedDataSource[i], path };
                return updatedDataSource;
            });
        },
        origin(i, origin) {
            setDataSource(prevDataSource => {
                const updatedDataSource = [...prevDataSource];
                updatedDataSource[i] = { ...updatedDataSource[i], origin };
                return updatedDataSource;
            });
        },
        process(i, process) {
            setDataSource(prevDataSource => {
                const updatedDataSource = [...prevDataSource];
                updatedDataSource[i] = { ...updatedDataSource[i], process };
                return updatedDataSource;
            });
        },
        progress(i, progress) {
            setDataSource(prevDataSource => {
                const updatedDataSource = [...prevDataSource];
                updatedDataSource[i] = { ...updatedDataSource[i], progress };
                return updatedDataSource;
            });
        },
        status(i, status) {
            setDataSource(prevDataSource => {
                const updatedDataSource = [...prevDataSource];
                updatedDataSource[i] = { ...updatedDataSource[i], status };
                return updatedDataSource;
            });
        },

        delete(i) {
            const newData = dataSource.filter(arr => arr !== dataSource[i]);
            setDataSource(newData);
        }
    }

    const mock = [{
        origin: 'A:/stable-diffusion-webui/outputs/txt2img-images/2023-07-08/00000-1613037609.png',
        process: '',
        path: 'D:/xxx/xxx/xxx',
        progress: parseInt('100%'.replace('%', '')),
        log: 'exit0'
    }, {
        origin: 'A:/stable-diffusion-webui/outputs/txt2img-images/2023-07-08/00001-2967784565.png',
        process: '',
        path: 'D:/xxx/xxx/xxx',
        progress: parseInt('100%'.replace('%', '')),
        log: 'exit0'
    }]

    const handleStop = () => {
        ipcRenderer.sendMessage('esrgan', { kill: true });
        setStart(false);
    }

    const startProcess = () => {
        var count = 0;
        setStart(true);
        sendCommand(count);
        ipcRenderer.on('esrganStdout', m => {
            changeColumn.status(count, m.data);
            changeColumn.progress(count, parseInt(m.data.replace('%', '')));
            console.log(m);
        })

        ipcRenderer.on('esrganExit', m => {
            changeColumn.status(count, '');
            changeColumn.progress(count, 100);
            changeColumn.process(count, `${dataSource[count].path}_optimization.png`);
            count++
            if (count >= dataSource.length) {
                setStart(false);
                return;
            }
            sendCommand(count);
        })
    }

    const sendCommand = (i) => {
        console.log(i);
        ipcRenderer.sendMessage('esrgan', {
            command: true,
            path: dataSource[i].path,
            model,
        })
    }

    return (
        <Card title=""
            headStyle={{
                position: 'sticky', top: 60, zIndex: 10, backdropFilter: 'blur(10px)', backgroundColor: 'rgba(255,255,255,.8)'
            }}
            extra={<Space size="small">
                <Button onClick={startProcess} type="primary" loading={start} disabled={fileLists.length == 0}></Button>
                <Button onClick={handleStop} danger disabled={!start}></Button>
            </Space>}>
            <Table dataSource={dataSource}
                columns={[{
                    title: '',
                    key: 'path',
                    dataIndex: 'path',
                    width: '20%',
                    render: (n, { path }) => (<div style={{ wordBreak: 'break-word' }}>{path}</div>)
                }, {
                    title: '',
                    key: 'origin',
                    render: (n, { origin }) => (<Image src={origin}></Image>),
                    width: '25%'
                }, {
                    title: '',
                    key: 'process',
                    width: '25%',
                    render: (n, { process }) => (process ? <Image src={process}></Image> : ''),
                }, {
                    title: '',
                    key: 'status',
                    // dataIndex: 'status',
                    render: (n, { status, progress }) => (<>
                        <Progress percent={progress} />
                        {status}
                    </>),
                    width: '20%',
                }, {
                    title: '',
                    key: 'status',
                    // dataIndex: 'status',
                    render: (n) => (<Popconfirm title="" onConfirm={() => {
                        const newData = dataSource.filter(arr => arr !== n);
                        setDataSource(newData);
                    }}>
                        <a></a>
                    </Popconfirm>),
                    width: '10%',
                }
                ]} />

        </Card>
    )
}
